    poppins: 300,400,600,600,800
    rubik: 300,400,500,600
} */

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700;800&family=Rubik:wght@300;400;500;600&display=swap');

:root {
  --primary-color: #ff6b6b;
  --secondary-color: #7941fa;
  --secondary-dark-color: #453c5c;

  --white-color: #ffffff;
  --light-bg-color: #f2f3f5;
  --light-text-color: #7c899a;
  --border-color: #e5e8ec;
  --dark-color: #0a021c;
  --bg-color: #050706;
  --text-main-color: #fdc200;

  --font-small: 13px;
  --font-smaller: 11px;

  --percent100: 100%;
  --percent50: 50%;

  --fw3: 300;
  --fw5: 500;
  --fw6: 600;
  --fw7: 700;
  --fw8: 800;

  --trans-background-color: background-color .3s, color .3s;
  --trans-background: background-color .3s;
  --trans-color: color .3s;
}

@keyframes pulse-border {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(1.5);
    opacity: 0;
  }
}

* {
  margin: 0;
  padding: 0;
}

*,
::before,
::after {
  box-sizing: border-box;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  color: var(--white-color);
  background-color: var(--bg-color);
}

a {
  text-decoration: none;
  color: inherit;
  -webkit-tap-highlight-color: transparent;
}

ul {
  list-style: none;
}

img {
  max-width: var(--percent100);
  vertical-align: middle;
}

strong {
  font-weight: var(--fw8);
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input::placeholder {
  font: inherit;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Poppins";
}

h1 {
  font-size: calc(1.3em + 1vw);
  font-weight: var(--fw8);
  line-height: 1;
}

h2 {
  font-size: 2.5em;
}

h3 {
  font-size: 1.2em;
  font-weight: var(--fw7);
}

h4 {
  font-size: 1em;
  font-weight: var(--fw6);
}

/* =================================================================
* REUSABLE SELECTORS
*
==============================================================*/

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 0.938em;
}

.column {
  margin-left: -0.938em;
  margin-right: -0.938em;
}

.column .row {
  padding: 0 0.938em;
}

.flexwrap {
  display: flex;
  flex-wrap: wrap;
}

.flexcenter {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flexitem {
  display: flex;
  align-items: center;
}

.flexcol {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.main-link a:hover {
  color: var(--secondary-color)
}

.second-link a:hover {
  color: var(--light-text-color);
}

.icon-small,
.icon-large {
  display: flex;
  align-items: center;
  padding: 0 0.25em;
  font-size: normal;
}

.icon-small {
  font-size: 1.25em;
  margin-left: auto;
}

.icon-large {
  font-size: 1.75em;
  margin: 0 0.75em 0 0;
}

.mobile-hide {
  display: none;
}

.object-cover img {
  position: absolute;
  object-fit: cover;
  width: var(--percent100);
  height: var(--percent100);
  display: block;
}

.products .media {
  position: relative;
  flex-shrink: 0;
  overflow: hidden;
}

.primary-button,
.secondary-button,
.light-button {
  font-size: var(--font-small);
  padding: 0.9em 2em;
  height: auto;
  width: fit-content;
  border-radius: 2em;
  transform: var(--trans-background-color);
}

.primary-button {
  background-color: var(--primary-color);
  color: var(--white-color);
}

.primary-button:hover {
  background-color: var(--dark-color);
}

.secondary-button {
  background-color: var(--secondary-dark-color);
  color: var(--white-color);
}

.secondary-button:hover {
  background-color: var(--light-bg-color);
  color: var(--secondary-dark-color);
}

.light-button {
  background-color: var(--light-bg-color);
}

.light-button:hover {
  background-color: var(--secondary-dark-color);
  color: var(--white-color);
}

.view-all {
  font-size: var(--font-small);
  display: flex;
  gap: 3em;
  transition: var(--trans-color);
}

.mini-text {
  font-size: var(--font-smaller);
  color: var(--light-text-color);
}

/*========== 
* MINES
============*/

/*========== 
* INFO
============*/

.walletbutton {
  color: #fcc400 !important;
  border-color: #fcc400 !important;
  background-color: var(--bg-color) !important;
}

.info {
  margin-top: 2rem;

}

.border {
  flex-direction: column;
  padding: 2.5em;
  border-radius: 7px;
  max-width: 460px;
  flex: 0 0 100;
  background: #25262acf;
  margin-top: 1em;

}

.info .border {
  margin-bottom: 5px;
}

.info h2 {
  color: var(--text-main-color);
  text-transform: uppercase;
  margin-bottom: 2em;
  font-size: 1.5em;

}

.info .row {
  display: flex;
}

.info .row:nth-last-child(even) {
  justify-content: flex-end;
}

.info .mission {
  float: right;
}

.info .mission ul {
  list-style: disc;
  padding-left: 2em;
  margin-top: 2em;

}



.info p {
  font-size: 1em;
  line-height: 2em;
}

.info .token .left {
  flex: 3;
}

.info .token .right {
  flex: 1;
}

.info .token .right .image {
  width: 50%;
}

.info .token .right img {
  object-fit: cover;
  width: var(--percent100);
  height: var(--percent100);
  display: block;
}

.creator .right {
  flex: 1;
}

.creator .left {
  flex: 2
}

.creator .flexitem {
  margin: 0 auto;
}

.creator .left h2 {
  color: var(--text-main-color);
  font-size: 30px;
  font-weight: var(--fw7);
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}

.token {
  margin-top: 1em;
}

.token .main-div {
  width: 90%;
  max-width: 800px;
  max-height: 700px;
  position: relative;
  background-color: #ff9635;
  color: var(--dark-color);
  margin: 0 auto;
  z-index: 20;
  padding: 2em;
  -webkit-box-shadow: 8px 5px 0px 0px #ff9635a8;
  box-shadow: 8px 5px 0px 0px #ff9635a8;
}

.token .main-div h2 {
  text-transform: uppercase;
  margin-bottom: 1em;
  text-align: center;
  font-size: 1.5em;
}

.token .main-div h5 {
  font-weight: 600;
  font-size: 1.2em;
  letter-spacing: 0.2em;
  text-align: center;
  margin-bottom: 0.5em;
}

.main-div p {
  color: var(--dark-color);
  letter-spacing: 0.2rem;
  font-family: "Roboto-Regular";
  font-weight: normal;
  margin-bottom: 0.5em;
  text-align: center;
}

.lead {
  font-size: 1em;
  line-height: 2em
}

.main-div a {
  text-decoration: underline;
}

.main-div a:hover {
  color: #c4cac4
}

.whitepaper {
  text-align: center;
  margin: 2em auto;
}

.whitepaper span {
  width: 300px;
  background: #ffa500;
  color: white;
  padding: 10px;
  cursor: pointer;
  text-transform: uppercase;
  box-shadow: 2px 2px 0px 0px #ffffff;
  border-radius: 4px;
}

.donation {
  margin-top: 2em;
}

.donation .row {
  width: 75%;
  margin: 0 auto;
}

.donation .row h2 {
  color: var(--text-main-color);
  font-size: 14px;
  font-weight: var(--fw7);
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  margin-bottom: 0.5em;
}

.donation .row p {
  padding: 5px;
  margin-bottom: 10px;
}

.donation .wallet {
  text-align: center;
}

.donation .wallet span {
  color: white;
  background: #ffa500;
  padding: 8px 25px;
  font-size: 20px;
  border-radius: 4px;
  display: inline-block;
  cursor: pointer;
  font-weight: bold;
}

.roadmap {
  margin-top: 3em;
}

.roadmap .flexitem {
  margin-bottom: 1em;
}

.roadmap h4 {
  color: var(--white-color);
  font-size: 1.5em;
  font-weight: var(--fw7);
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  margin-bottom: 0.5em;
}

.roadmap .item h4 {
  color: var(--white-color);
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  margin-bottom: 0.5em;
}




.roadmap ul {
  list-style-type: disc;
  margin-top: 1.5em;
  padding-left: 2em;
  color: #ffa500;

}

.roadmap li {
  font-size: 1.3em;
  text-transform: capitalize;
}

.listing {
  text-align: center;

}

.listing .list {
  font-size: 0.875em;
  font-weight: var(--fw6);
  background-color: var(--primary-color);
  color: var(--white-color);
  border-radius: 7px;
  cursor: pointer;
  transition: var(--trans-background);
  padding: 8px 25px;
  -webkit-box-shadow: 2px 2px 0px 0px #ffffff;
  box-shadow: 2px 2px 0px 0px #ffffff;
  margin-right: 1em;
  margin-bottom: 1em;
}

.listing .list:hover {
  background-color: #df7575;
}

.listing .flexwrap {
  justify-content: space-around;
}

.box .content {
  text-align: center;
}

.header-top .main-links {
  color: var(--light-text-color);
}

.header-top .main-links i {
  margin-right: 5px;
}

/*========== 
* HEADERS
============*/

:where(.off-canvas, header) li>a {
  display: flex;
  position: relative;
  line-height: inherit;
  transition: var(--trans-color);
}

/*========== 
* HEADERS
* header top
============*/
.header-top .wrapper {
  font-size: var(--font-small);
  font-weight: var(--fw3);
  justify-content: space-between;
  line-height: 42px;
  padding: 3px 0;
}

.header-top .wrapper ul {
  gap: 2em;
}

.header-top li {
  position: relative;
}

.header-top ul ul {
  position: absolute;
  left: 1em;
  line-height: 2em;
  background-color: var(--white-color);
  border: 1px solid var(--border-color);
  z-index: 1;
  display: none;
}

:where(.header-top, .thetop-nav) .right ul ul li a {
  padding: 0.25em 1em;
}

:where(.header-top, .thetop-nav) .right ul ul li.current a {
  background-color: var(--border-color);
}

.header-top li:hover ul {
  display: block;
}

/*========== 
* HEADERS
* header nav
============*/

.header-nav {
  padding: 0.5em 0;
  margin-bottom: 1.5em;
  border-bottom: 1px solid var(--border-color);
}

.trigger {
  font-size: 1.5em;
  display: flex;
  padding: 0.25em;
  margin-left: 0.5em;
}

.logo a {
  font-size: 1.75em;
  font-family: 'Poppins';
  position: relative;
  font-weight: var(--fw8);
  display: flex;
  padding-left: 0.75em;
  margin-right: 2em;
}

.logo img {
  object-fit: cover;

}

.logocontainer {
  width: 50px;
  border-radius: var(--percent50);
  overflow: hidden;
}

.logocontainer::before {
  content: '';
  position: absolute;
  width: 50px;
  height: 51px;
  border-radius: var(--percent50);
  bottom: -2px;
  right: 0px;
  opacity: 4;
  border: solid 3px #fdc300;
  animation: pulse-border 1.5s linear infinite;

}


.circle {
  position: absolute;
  top: -5px;
  left: 0;
  width: 38px;
  height: 38px;
  border-radius: var(--percent50);
  z-index: -1;
}

.circle::before {
  content: '';
  position: absolute;
  width: 28px;
  height: 28px;
  border-radius: var(--percent50);
  background-color: var(--secondary-color);
  bottom: 5px;
  right: 5px;
  opacity: 4;

}

.header-nav nav>ul {
  line-height: 20px;
  gap: 20px;
  color: var(--white-color);
  font-weight: var(--fw6);
}

.fly-item {
  position: absolute;
  height: 13px;
  font-size: var(--font-smaller);
  padding: 3px;
  text-align: center;
  line-height: 10px;
  color: var(--white-color);
}

nav .fly-item,
.header-nav .mini-cart .price .fly-item {
  top: 50%;
  margin-top: -24px;
  width: 30px;
  border-radius: 3px;
  right: -32px;
  background-color: var(--primary-color);
}

.header-nav .right {
  position: relative;
  margin-left: auto;
}

.header-nav .right .icon-large {
  position: relative;
}

.header-nav .right .fly-item {
  top: 0;
  right: 14px;
  width: 14px;
  background-color: var(--secondary-dark-color);
  border-radius: var(--percent50);
}

.mega .flexcol {
  flex: 1;
  min-width: 100px;
  padding-right: 2.5em;
  margin-bottom: 1.5em;
  z-index: 1;
}

/*========== 
* HEADERS
* Header main depertment category
============*/

.header-main {
  background-color: var(--secondary-dark-color);
  padding: 1.5em 0;
  color: var(--white-color);
  text-align: center;
}

.header-main .wrapper {
  text-align: center;
}

.dpt-cat {
  position: relative;
  z-index: 10;
}

.dpt-cat .dpt-head {
  position: relative;
  width: 300px;
  padding: 0.75em 1.5em;
  background-color: var(--primary-color);
  border-radius: 7px 7px 0 0;
  color: var(--white-color);
  margin-bottom: -2.15em;
}

.dpt-cat .dpt-head .mini-text {
  color: var(--light-bg-color);
}

.dpt-cat .dpt-trigger {
  position: absolute;
  right: 0;
  top: 0;
  padding: 1.3em;
  height: 60px;
  width: 60px;
}

.dpt-menu>ul>li>a {
  font-size: var(--font-small);
  height: 46px;
  align-items: center;
}

.dpt-menu .has-child:hover ul {
  display: block;
}

.dpt-menu .has-child li a:hover {
  text-decoration: underline;
}

/*========== 
* HEADERS
* Header search form
============*/

.header-main .right {
  flex: 1;
}

form {
  position: relative;
}

form.search input {
  line-height: 3.25em;
  padding: 0 7em 0 4.5em;
  border: 0;
  outline: 0;
  width: var(--percent100);
  border-radius: 7px;
  font-size: 0.9em;
  font-weight: var(--fw3);
}

form.search :where(span, button) {
  position: absolute;
  top: 0;
  padding: 0.55em 1.5em;
  font-size: 1em;
  color: var(--light-text-color);
  height: var(--percent100);
}

form.search button {
  right: 0;
  border: 0;
  outline: 0;
  font-size: 0.875em;
  font-weight: var(--fw6);
  background-color: var(--primary-color);
  color: var(--white-color);
  border-radius: 0 7px 7px 0;
  cursor: pointer;
  transition: var(--trans-background);
}

form.search button:hover {
  background-color: var(--dark-color);
}

/*========== 
* HEADERS =================================================================
* Responsive Menu - Off Canvas
============*/

.site-off {
  position: fixed;
  width: 320px;
  height: var(--percent100);
  background-color: var(--white-color);
  overflow-y: auto;
  z-index: 1000;
  transform: translateX(-100%);
  visibility: hidden;
  transition: transform .4s, visibility .4s;
  will-change: transform, visibility;
}

.showmenu .site-off {
  transform: translateX(0);
  visibility: visible;
  transition: transform .4s 0s, visibility 0s 0s;
}

.off-canvas {
  width: var(--percent100);
  height: var(--percent100);
  touch-action: auto;
  padding: 1.5em;

}

.off-canvas .canvas-head {
  color: var(--white-color);
  justify-content: space-between;
  padding: 1.5em;
  margin: -1.5em -1.5em 1.5em;
  background-color: var(--secondary-dark-color);
  border-bottom-right-radius: 160px 25px;
}

.off-canvas .canvas-head .logo {
  z-index: 10;
  position: relative;
}

.off-canvas .canvas-head .logo .circle {
  opacity: .75;
}

.t-close {
  font-size: 1.25em;
  width: 32px;
  height: 32px;
  border-radius: var(--percent50);
  background-color: var(--secondary-dark-color);
  color: var(--white-color);
  transition: var(--trans-background-color);
}

.t-close:hover {
  background-color: var(--white-color);
  color: var(--secondary-dark-color);
  opacity: .75;
}

.off-canvas .dpt-head {
  font-size: var(--font-small);
  padding: 1em;
  margin-bottom: 1em;
  text-align: center;
  background-color: var(--light-bg-color);
  border-radius: 7px 7px 0 0;
}

.off-canvas .has-child> :where(ul, .mega) {
  font-size: var(--font-small);
  font-weight: var(--fw3);
  line-height: 28px;
  padding-left: 3em;

  height: auto;
  max-height: 0;
  overflow: hidden;
  transition: max-height .3s cubic-bezier(0.215, 0.610, 0.355, 1);
}

.off-canvas .expands> :where(ul, .mega) {
  max-height: 2000px;
}

.off-canvas .thetop-nav ul,
.off-canvas nav>ul {
  flex-direction: column;
  align-items: flex-start;
  font-size: var(--font-small);
  line-height: 35px;
  padding-top: 1.25em;
  margin: 1.25em 0;
  border-top: 1px solid var(--border-color);
}

.off-canvas nav .mega {
  padding: 0;
}

.off-canvas nav .mega .products {
  display: none;
}

.off-canvas .has-child .icon-small {
  padding: 0.5em;
  line-height: initial;
}

.off-canvas .dpt-menu .icon-small i {
  transform: rotate(90deg);
}

.off-canvas .has-child {
  width: var(--percent100);
}

.off-canvas .thetop-nav .right>ul> :where(:nth-child(4), li:nth-child(5))>a {
  display: none;
}

.off-canvas .thetop-nav .right ul ul {
  display: flex;
  flex-direction: row;
}

.off-canvas .thetop-nav .right ul ul li a {
  padding: 0.5em;
  border: 1px solid var(--border-color);
  line-height: 16px;
  margin: 0 0.5em 0.5em 0;
}

/*========== 
* SLIDER
============*/

.slider .item {
  position: relative;
}



.slider .mainimg {
  flex: 1;
  position: relative;
  width: 100%;

}

.slider .mainimg .image {
  border-radius: 50%;
  overflow: hidden;
}

.slider .text-content {
  flex: 2;
  position: relative;
  height: var(--percent100);
  justify-content: center;
  padding: 0 0 0 10%;
  z-index: 1;
}

.slider h2 {
  font-size: 1.5em;
}

.slider h4 {
  color: var(--white-color);

}

.text-content :where(h2, h3) span:first-child {
  font-weight: var(--fw3);
  color: var(--white-color);
}

.text-content :where(h2, h3) span:last-child {
  color: var(--text-main-color);
  text-transform: uppercase;
  letter-spacing: 5px;
}


/*========== 
* BRANDS
============*/
.brands .wrapper {
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 2em 0;

}

.brands .item img {
  opacity: .25;
  transition: opacity .3s;
}

.brands .item a:hover img {
  opacity: 1;
}


/*========== 
* Product block style
============*/

.sectop {
  justify-content: space-between;
  padding-bottom: 0.5em;
  margin-bottom: 2em;
  border-bottom: 1px solid var(--border-color);
}

.sectop h2 {
  position: relative;
  font-size: 1.5em;
  font-weight: var(--fw7);
}

.sectop h2 span:not(.circle) {
  padding: 0 0 0 2em;
}

.sectop h2 .circle {
  top: -5px;
}



/*========== 
* PRODUCT
* Global product
============*/

.products .offer {
  text-align: center;
  margin-bottom: 1.5em;

}

.products .offer p {
  text-transform: uppercase;
  margin-bottom: 0.5em;
}

.products .offer ul {
  gap: 1em;
}

.products .offer ul li {
  position: relative;
  width: 34px;
  height: 34px;
  padding: 0.5em;
  line-height: initial;
  color: var(--secondary-dark-color);
  border-radius: 5px;
}

.products .offer ul li:not(:last-child)::before {
  content: ':';
  position: absolute;
  right: -0.6em;
  color: var(--light-text-color);
}

.products :where(.image, .thumbnail) img {
  transition: transform .3s;
}

.products :where(.image, .thumbnail):hover img {
  transform: scale(1.1);
}

.products .hoverable {
  position: absolute;
  top: 0;
  right: 0;
}

.products .hoverable li a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  padding: 0.25em;
  margin: 0.25em;
  border-radius: var(--percent50);
  line-height: 1;
  background-color: var(--white-color);
  opacity: .5;
  transition: var(--trans-background-color), opacity .3s;
}

.products .item:hover .hoverable li a {
  opacity: 1;
}

.products .hoverable li a:hover {
  color: var(--white-color);
  background-color: var(--primary-color);
}

.products .hoverable li.active a:hover {
  background-color: var(--primary-color);
}

.products .hoverable li:not(.active) {
  transform: translateX(100%);
  opacity: 0;
  transition: transform .3s, opacity .2s;
}

.products .item:hover .hoverable li {
  transform: translateX(0);
  opacity: 1;
}

.products .hoverable li:last-child {
  transition-delay: .1s;
}

.products .discount {
  top: auto;
  right: 0;
  left: auto;
  bottom: 0;
  background-color: transparent;
  z-index: 1;
}

.products .discount::before {
  background-color: var(--dark-color);
}

.products .discount span {
  position: relative;
  font-size: var(--font-smaller);
  font-weight: var(--fw5);
  color: var(--white-color);
}

.products .content {
  display: flex;
  flex-direction: column;
}

.products:where(.big, .main, .one) .content {
  gap: 1em;
  margin-top: 1.25em;
}

.products .rating {
  display: flex;
  align-items: center;
  gap: 0.5em;
}

.products .rating .star {
  width: 80px;
  height: 20px;
  background-position-y: top;
  background-repeat-y: no-repeat;
}

.products .item:where(:nth-child(3), :nth-child(3)) .stars {
  width: 64px;
}

.products h3 {
  font-family: 'Rubik';
  font-size: 1em;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  display: -webkit-box;
  -webkit-box-orient: vertical;
}

.products h3 a:hover {
  text-decoration: underline;
}

.products .price .current {
  font-size: calc(1em + 1vw);
  color: var(--primary-color);
  margin-right: 0.25em;
}

.products .price .normal {
  color: var(--light-text-color);
  text-decoration: line-through;
}

.products .stock .qty {
  display: flex;
  justify-content: space-between;
}

.products .stock .bar {
  height: 3px;
  margin-top: 1em;
  background-color: var(--border-color);
  overflow: hidden;
}

.products .stock .available {
  height: 3px;
  width: 21%;
  background-color: var(--secondary-color);
}

.products .item {
  display: flex;
  position: relative;
}

.products .stock-danger {
  color: var(--primary-color);
}

.flexwrap .row {
  flex: 0 0 100%;
  width: 100%;
  margin-bottom: 2em;
}














/*========== 
* PRODUCT
* Big product
============*/

.products.big .item {
  flex-direction: column;
  padding: 2.5em;
  border: 2px solid var(--secondary-dark-color);
  border-radius: 7px;
  max-width: 460px;
  margin: 0 auto;
}


/*========== 
* PRODUCT
* Mini product
============*/

.products.mini .item {
  margin-bottom: 2em;
}

.products.mini .media {
  width: 130px;
  height: 160px;
  margin-right: 1.25em;
}

.products.mini .content {
  margin: 0;
  gap: 0.75em;
}

.products:where(.mini, .main) h3 {
  font-weight: 400;
}


/*========== 
* PRODUCT
* Main product
============*/

.products.main .item {
  flex-direction: column;
  flex: 0 0 100%;
  padding: 0 0.938em;
  padding-bottom: 2em;
}

.products.main .media {
  height: 390px;
}

.products.main .footer {
  position: absolute;
  left: 0;
  right: 0;
  padding: 0 1em 1em;
  border-radius: 7px;
  background: linear-gradient(0deg var(--light-bg-color) 0%, var(--white-color) 70%);
  opacity: 0;
  visibility: hidden;
  will-change: opacity;
  transition: all .2s ease-in-out;
}

.products.main .item:hover .footer {
  opacity: 1;
  visibility: visible;
}

.products.main .footer ul {
  list-style: disc;
  padding: 1.25em 0 0 1.25em;
  line-height: 1.8;
  border-top: 1px solid var(--border-color);
  margin-top: 145px;
}

.products.main .item:hover .content>*:not(.footer) {
  z-index: 3;
}


/*========== 
* BANNER
============*/
.banners .item {
  position: relative;
  background-color: var(--white-color);
  border: 1px solid var(--border-color);

}

.banner .image {
  text-align: right;
}

.banner .get-gray {
  background-color: var(--border-color);
}

.banner :where(.text-content, .over-link) {
  position: absolute;
  top: 0;
  left: 0;
  width: var(--percent100);
  height: var(--percent100);
}

.banner .text-content {
  padding: 1.5em 2.5em;
}

.banner h3 {
  font-size: calc(1em + 0.5vw);
  font-weight: var(--fw3);
}

.banner h3 span {
  font-size: 80%;
}

.banner .primary-button {
  z-index: 3;
}

.banners .product-categories .item {
  display: flex;
  padding: 1.5em;
}

.banners .product-categories .image {
  max-width: 150px;
}

.mini-links ul {
  font-size: var(--font-small);
  color: var(--light-text-color);
  margin-top: 1em;
}

.banners .second-links {
  margin-top: 2em;
}

.mini-links li a:hover {
  text-decoration: underline;
}


/*========== 
* FOOTER
============*/
/* nEWSLETTER */

.newsletter {
  padding: 2.5em 0;
  background-color: var(--dark-color);
  color: var(--white-color);
}

.newsletter h3 {
  font-size: 1.5em;
  text-transform: uppercase;
}

.newsletter p {
  font-weight: var(--fw3);
  color: var(--border-color);
  margin: 0.5em 0 1.5em;
}

.newsletter p strong {
  color: var(--primary-color);
}

.newsletter form {
  max-width: 500px;
  width: var(--percent100);
}

/* WIDGETS */

.widgets {
  padding: 3.5em 2.5em;
  background-color: var(--light-bg-color);
}

/* FOOTER INFO */

.footer-info {
  padding: 3.5em 0 5em;
}

.footer-info .wrapper .flexcol {
  align-items: center;
  gap: 0;
}

.footer-info ul li a {
  font-size: 1.25em;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: var(--light-bg-color);
  border-radius: var(--percent50);
  margin: 0.25em;
  transition: var(--trans-background);
}

.footer-info ul li a:hover {
  background-color: var(--border-color);
}

.footer-info .logo a {
  margin: 0 0 0 -0.75em;
}

.footer-info .mini-text {
  margin-top: 2em;
  text-align: center;
}

.socials {
  list-style: none;

}

.socials li {
  margin-right: 1em;
}

/*========== 
* BOTTOM MENU
============*/

.menu-bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: 60px;
  background-color: var(--white-color);
  box-shadow: 0 -2px 10px rgba(0 0 0 / 10%);
  z-index: 900;
}

.menu-bottom nav li {
  flex: 1;
}

.menu-bottom nav li a {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5em 0;
  color: var(--light-text-color);
  transition: var(--trans-color);
}

.menu-bottom nav li a:hover {
  color: var(--dark-color);
}

.menu-bottom nav li a span {
  font-size: var(--font-smaller);
}

.menu-bottom nav li a i {
  font-size: 1.25em;
}

.menu-bottom .fly-item {
  right: 50%;
  width: 16px;
  border-radius: var(--percent50);
  margin-right: -20px;
  background-color: var(--secondary-dark-color);
}

/* search */
.search-bottom {
  position: fixed;
  bottom: 60px;
  width: var(--percent100);
  padding: 2.5em 0;
  background-color: var(--secondary-dark-color);
  z-index: 1000;
  visibility: hidden;
  opacity: 0;
  will-change: opacity, visibility;
}

.showsearch .search-bottom {
  visibility: inherit;
  opacity: 1;
}

.search-bottom .t-close {
  position: absolute;
  top: -54px;
  right: 0;
}

.search-bottom .t-close:hover {
  background-color: var(--primary-color);
}

/*========== 
* OVERLAY
============*/
.overlay {
  position: fixed;
  width: var(--percent100);
  height: var(--percent100);
  top: 0;
  left: 0;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.4);
  visibility: hidden;
  opacity: 0;
  will-change: opacity, visibility;
}

:where(.showmenu, .showmodal) .overlay {
  opacity: 1;
  visibility: inherit;
}



/*========== 
* SINGLE PAGE
============*/

/* depertment menu */
.page-home .dpt-cat .dpt-trigger,
.dpt-trigger .ri-close-line,
.showdpt .dpt-trigger .ri-menu-3-line {
  display: none;
}

.showdpt .dpt-trigger .ri-close-line {
  display: block;
}

/* breadcrumb */

.breadcrumb {
  font-size: var(--font-small);
  margin-bottom: 2em;
}

.breadcrumb li:not(:last-child)::after {
  content: '/';
  padding: 0 0.35em;
}

.breadcrumb li:last-child {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: var(--light-text-color);
}

/* product details */

.products.one .item {
  flex-direction: column;
}

.products.one .price .discount {
  font-size: 1em;
  font-weight: var(--fw7);
  line-height: 1;
  position: absolute;
  top: 0;
  bottom: auto;
  padding: 1em;
  z-index: 2;
  background-color: var(--dark-color);
  color: var(--white-color);
  border-radius: var(--percent50);
  margin: 0.5em;
}

.products.one :where(.big-image, .small-image) {
  overflow: hidden;
}

.products.one .big-image {
  position: relative;
  margin-bottom: 1em;
}

.products.one :where(.big-image, .small-image) img {
  object-fit: cover;
  width: var(--percent100);
  height: var(--percent100);
  display: block;
}

.products.one .thumbnail-show {
  position: relative;
  width: 130px;
  height: 110px;
  overflow: hidden;
  margin: 0 2em 2em 0;
}

.products :is(.swiper-button-next, .swiper-button-prev) {
  outline: 0;
  color: var(--secondary-dark-color);
  transition: var(--trans-background), transform .3s;
}

.products .big-image:hover .swiper-button-next {
  transform: translateX(10px);
}

.products .big-image:hover .swiper-button-prev {
  transform: translateX(-10px);
}

.products :is(.swiper-button-next, .swiper-button-prev)::after {
  font-size: 1.5em;
}

.products .big-image:hover :is(.swiper-button-next, .swiper-button-prev) {
  background-color: var(--light-bg-color);
}

/* product description */

.products.one .available {
  font-size: var(--font-small);
  font-weight: var(--fw5);
  padding: 0.5em;
  margin-right: 1em;
  border-radius: 3px;
  color: #10ac84;
  background-color: var(--light-bg-color);
}

.products.one .add-review {
  color: var(--dark-color);
}

.products.one .price {
  display: flex;
  gap: 1em;
  align-items: center;
  flex-wrap: wrap;
}

.products.one .price .current {
  font-size: 2.5em;
}

.products .variant form {
  display: flex;
  margin-top: 0.5em;
}

.products .variant form p {
  position: relative;
  margin: 0 0.5em 0.5em;
}

.products :where(.variant, .actions) .circle {
  display: block;
  position: static;
  top: 0;
  margin: 0;
  cursor: pointer;
  z-index: 1;
}

:where(.products .variant, .filter-block:not(.pricing)) input {
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
  position: absolute;
  height: 0;
  width: 0;
}

.products .colors .variant label::before {
  opacity: 1;
}

.single-product .variant form p input:checked+label {
  background-color: transparent;
  border: 2px solid var(--dark-color);
  color: var(--white-color);
}

.products .sizes .variant label::before {
  background-color: var(--white-color);
}

.products .sizes .variant label span {
  position: absolute;
  top: 0;
  left: 0;
  width: var(--percent100);
  height: var(--percent100);
  font-size: 0.85em;
  display: flex;
  align-items: center;
  justify-content: center;
}

.single-product .sizes .variant form p input:checked+label::before {
  background-color: var(--dark-color);
  opacity: 1;
}

.products .actions {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2em;
}

.products .qty-control {
  width: fit-content;
  padding: 0.5em;
  border: 1px solid var(--border-color);
  margin: 0 2em 2em 0;
}

.products .actions :where(input, button) {
  font-size: 1.25em;
  outline: 0;
  border: 0;
}

.products .actions input {
  width: 50px;
  text-align: center;
}

.products .qty-control button::before {
  background-color: transparent;
}

.products .actions .button-cart {
  flex: 1;
  min-width: 200px;
}

.products .actions .button-cart button {
  width: var(--percent100);
  cursor: pointer;
}

.wish-share a {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-weight: var(--fw7);
  margin: 1em 2em 1em 0;
  transition: var(--trans-color);
}

/* product review */

.products .collapse .has-child>a {
  position: relative;
  font-weight: var(--fw7);
  text-transform: uppercase;
  padding: 1em 1.25em;
  border-top: 1px solid var(--border-color);
  gap: 1em;
  align-items: flex-start;
}

.products .collapse .has-child>a::before {
  content: '+';
  position: absolute;
  left: 0;
}

.products .collapse .content {
  margin: 0 0 1.5em 2em;
  font-size: var(--font-small);
}

.products .collapse .content li span:first-child {
  min-width: 100px;
  display: inline-flex;
  font-weight: var(--fw7);
  text-transform: uppercase;
}

.products .collapse table {
  line-height: 3em;
}

.products .collapse table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid var(--dark-color);
}

.products .collapse table :where(th, td) {
  border-bottom: 1px solid var(--border-color);
  padding-left: 2em;
}

.products .collapse .content {
  display: none;
}

.products .collapse .expand .content {
  display: flex;
}

.products .collapse .expand>a::before {
  content: '-';
}

/* Product Review Form */

.products .review h4 {
  font-size: 2em;
  color: var(--light-text-color);
  padding-top: 1em;
  margin: 1em 0 0.5em;
  border-top: 1px solid var(--border-color);
}

.products .review-block-head>a {
  display: block;
  font-size: 1.25em;
  width: var(--percent100);
  margin-top: 1em;
  text-align: center;
}

.products .review-block-head .rate-sum {
  position: relative;
  font-size: 4em;
  font-weight: var(--fw7);
  padding-right: 0.5em;
}

.products .review-block-head .rate-sum::before {
  content: '';
  width: 2px;
  height: 50px;
  display: block;
  position: absolute;
  top: 10px;
  right: 13px;
  transform: rotate(22deg);
  background-color: var(--light-bg-color);
}

.products .review-block-body .item {
  gap: 1em;
  padding-bottom: 2.5em;
  margin-top: 3.5em;
}

.products .review-block-body :where(.person, .review-title) {
  font-weight: var(--fw7);
  text-transform: uppercase;
}

.products .review-block-body :where(.review-title, .view-all) {
  font-size: 1.25em;
  justify-content: end;
}

.products .review-block-body .item:not(:last-child) {
  border-bottom: 1px solid var(--border-color);
}

/* product review form */

.review-form .rating {
  flex-wrap: wrap;
}

.review-form p {
  font-size: 1.25em;
  margin-right: 1em;
}

.review-form .rate-this input {
  display: none;
}

.review-form .rate-this label {
  float: right;
  font-size: 2em;
  color: var(--secondary-dark-color);
}

.rate-this>input:checked~label,
.rate-this:not(:checked)>label:hover,
.rate-this:not(:checked)>label:hover~label {
  color: #f1c48c;
}

.rate-this>input:checked+label:hover,
.rate-this>input:checked~label:hover,
.rate-this>input:hover~input:checked~label,
.rate-this>input:checked~label:hover~label {
  color: #ffed85;
}

.review-form form {
  margin-top: 2.5em;
}

form p {
  display: flex;
  flex-direction: column;
  margin-bottom: 2em;
}

form :where(input, textarea) {
  line-height: 1;
  padding: 1em;
  border: 1px solid var(--border-color);
  outline: 0;
}

form label {
  font-weight: var(--fw5);
  margin-bottom: 0.5em;
  text-transform: uppercase;
}


/*========== 
* PAGE CATEGORY
============*/
.page-title {
  margin-bottom: 3em;
}

.single-category .holder {
  display: flex;
  flex-direction: column;
}

.single-category .single,
.single-category .section {
  flex: 1 0 100%;
}

/* filter */

.filter-block {
  margin-bottom: 1em;
  padding-bottom: 1em;
}

.filter-block:not(:last-child) {
  border-bottom: 1px solid var(--border-color);
}

.filter-block h4 {
  font-size: 1.25em;
  margin-bottom: 0.5em;
}

.filter-block li {
  display: flex;
  font-size: 0.9em;
  position: relative;
  line-height: 2;
}

.filter-block label .checked {
  width: 16px;
  height: 16px;
  position: relative;
  line-height: 0;
  display: inline-block;
  border: 2px solid var(--secondary-dark-color);
  vertical-align: text-top;
  margin: 0 7px 0 0;
  cursor: pointer;
}

.filter-block label .checked::before {
  content: '';
  position: absolute;
  width: 8px;
  height: 8px;
  background-color: var(--secondary-dark-color);
  top: 2px;
  left: 2px;
  opacity: 0;
}

.filter-block input:checked+label .checked::before {
  opacity: 1;
}

.filter-block li .count {
  margin-left: auto;
  color: var(--light-text-color);
}

.bycolor label[for='cored']::before {
  background-color: var(--primary-color);
}

.bycolor label[for='colight']::before {
  background-color: var(--border-color);
}

.bycolor label:checked+label {
  background-color: transparent;
  border: 2px solid var(--dark-color);
}

input[type='range'] {
  -webkit-appearance: none;
  width: var(--percent100);
}

input[type="range"]:focus {
  outline: 0;
}

input[type='range']::-webkit-slider-runnable-track {
  width: var(--percent100);
  height: 5px;
  cursor: pointer;
  background-color: var(--border-color);
  box-sizing: none;
  border: 0;
}

input[type='range']::-webkit-slider-thumb {
  z-index: 2;
  position: relative;
  height: 18px;
  width: 14px;
  background-color: var(--primary-color);
  -webkit-appearance: none;
  margin-top: -7px;
}

.byprice .price-range {
  display: flex;
  justify-content: space-between;
  color: var(--light-text-color);
  margin-top: 0.5em;
}

/* head and product block */
.cat-description p {
  font-weight: var(--fw3);
  font-size: 0.9em;
}

.cat-navigation {
  font-size: 0.85em;
  margin: 2em 0;
  justify-content: flex-end;
  gap: 1em;
}

.cat-navigation .item.filter {
  margin-right: auto;
}

.cat-navigation>div {
  position: relative;
}

.cat-navigation .item.filter a {
  align-items: center;
  text-transform: uppercase;
}

.cat-navigation ul {
  display: none;
  position: absolute;
  left: 0;
  min-width: var(--percent100);
  width: max-content;
  z-index: 14;
}

.cat-navigation .label,
.cat-navigation ul li {
  display: flex;
  gap: 0.5em;
  padding: 0.5em 1em;
  transition: var(--trans-color);
}

:where(.item-sortir, .item-options) .label,
.cat-navigation ul {
  color: var(--light-text-color);
  background-color: var(--light-bg-color);
  cursor: pointer;
}

.cat-navigation :where(label, ul li):hover {
  color: var(--dark-color);
}

:where(item-sortir, .item-options):hover ul {
  display: block;
}

.load-more {
  margin: 2em 0 4em;
}

.filter {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  padding: 2em;
  max-width: 400px;
  width: 90%;
  background-color: var(--white-color);
  overflow: auto;
  z-index: 1000;
  box-shadow: rgb(0 0 0 / 30%) 0 10px 50px;
  visibility: hidden;
  opacity: 0;
}

.filter.show {
  visibility: visible;
  opacity: 1;
}

.mini-cart {
  position: absolute;
  width: 400px;
  top: 100%;
  right: 0;
  background-color: var(--white-color);
  z-index: 1000;
  box-shadow: rgb(0 0 0 / 30%) 0 10px 50px;
  visibility: hidden;
  opacity: 0;

}

.iscart:hover .mini-cart {
  visibility: hidden;
  opacity: 1;
}

:is(.mini-cart .products, .products.cart, .checkout .products) .thumbnail img {
  transform: none;
}

.mini-cart ul :where(li, .price) {
  position: relative;
}

.mini-cart .content {
  width: var(--percent100);
  height: var(--percent100);
  touch-action: auto;
  padding: 1.5em;
}

.mini-cart .cart-head {
  font-size: 1.5em;
  font-weight: var(--fw5);
  color: var(--light-text-color);
  padding-bottom: 0.5em;
  margin-bottom: 1em;
  border-bottom: 1px solid var(--border-color);
}

.mini-cart .cart-body {
  padding: 0 1.5em;
  margin: 0 -1.5em;
  max-height: 450px;
  overflow: auto;
}

:where(.mini-cart, .products.cart, .checkout) .thumbnail {
  position: relative;
  width: 80px;
  height: 80px;
  flex-shrink: 0;
  margin-right: 1em;
}

.mini-cart .item-content p {
  font-size: var(--font-small);
  font-weight: var(--fw5);
  margin-bottom: 1.5em;
  line-height: 1.2;
}

.mini-cart .item-remove {
  margin-left: 1em;
  flex: 1;
  justify-content: flex-end;
}

.mini-cart .subtotal {
  display: flex;
  flex-direction: column;
  padding-top: 1em;
  margin-bottom: 2em;
  border-top: 1px solid var(--border-color);
}

.mini-cart .subtotal p:last-child {
  font-size: 2em;
}

.mini-cart .actions a {
  display: block;
  width: var(--percent100);
  text-align: center;
  margin-bottom: 1em;
}

.mini-cart.show {
  visibility: visible;
  opacity: 1;
  position: fixed;
  top: 0;
  bottom: 0;
  max-width: 400px;
  width: 90%;
}

/*========== 
* Page cart
============*/
.products.cart form {
  width: var(--percent100);
}

.products.cart table thead th {
  vertical-align: middle;
  background-color: var(--light-bg-color);
}

.products.cart table :where(th, td) {
  padding: 2em 1em;
  text-align: left;
}

.products.cart table td:first-child {
  padding-left: 0;
}

.products.cart table tbody td:first-child {
  padding-left: 0;
}

.products.cart table td :where(.content, .qty-control, p) {
  margin: 0;
  padding: 0;
}

.products.cart .qty-control :where(button, input) {
  width: 32px;
  height: 32px;
  padding: 0.25em;
  text-align: center;
  outline: 0;
  border: 0;
  background-color: transparent;
}

.products.cart .qty-control button {
  cursor: pointer;
}

.products.cart table tbody tr {
  border-bottom: 1px solid var(--border-color);
}

.cart-summary .item {
  background-color: var(--light-bg-color);
}

.cart-summary .coupon {
  position: relative;
}

.cart-summary .coupon input {
  font-size: 1.1em;
  outline: 0;
  width: var(--percent100);
  padding: 0 1.5em;
  line-height: 50px;
  background-color: var(--white-color);
  border: 3px solid var(--dark-color);
}

.cart-summary .coupon button {
  position: absolute;
  top: 0;
  right: 0;
  border: 0;
  outline: 0;
  font-size: 1em;
  padding: 0 2.5em;
  line-height: 56px;
  background-color: var(--dark-color);
  color: var(--white-color);
  cursor: pointer;
}

.cart-summary .shipping-rate {
  padding: 0 2em 0 1em;
}

.cart-summary .shipping-rate {
  padding: 0 2em 0 1em;
}

.cart-summary .shipping-rate .has-child>a {
  font-size: 1em;
  margin: 2em 0;
  border: 0;
}

.cart-summary .content form {
  display: flex;
  flex-direction: column;
  margin-bottom: 1em;
}

.styled :where(input, select, textarea) {
  padding: 0.75em;
  outline: 0;
  background-color: var(--white-color);
  border-width: 0 0 3px 0;
  border-style: solid;
  border-color: var(--border-color);
}

.products .cart-summary .variant form label {
  position: relative;
  border: 0;
  background-color: var(--white-color);
  transform: scale(.5) translateX(-25px);
}

.cart-summary .variant p span {
  position: absolute;
  top: 0;
  left: 20px;
  line-height: 3;
}

.cart-summary .variant input:checked+label::before {
  opacity: 1;
}

.products.cart .cart-total table tr>* {
  padding: 0;
}

.products-cart .cart-total table td {
  text-align: right;
}

.cart-summary .cart-total {
  padding: 2em;
  background-color: var(--border-color);
  line-height: 2em;
}

.cart-summary .cart-total table {
  width: var(--percent100);
}

.cart-summary .cart-total table tr {
  display: flex;
  justify-content: space-between;
}

.cart-summary .grand-total td {
  font-size: 2em;
  font-family: 'Poppins';
  font-weight: var(--fw8);
  line-height: 2em;
}

.cart-summary .cart-total>a {
  width: var(--percent100);
  display: block;
  text-align: center;
  margin-top: 2em;
  font-size: 1.25EM;
}

/*========== 
* PAGE CHECKOUT
============*/
.checkout {
  margin: 5em 0;

}

.checkout .left {
  padding: 4em;
  background-color: var(--light-bg-color);
  box-shadow: 0 15px 70px -8px rgb(0 0 0 / 20%);
}

.checkout .item {
  width: var(--percent100);
}

.checkout .left h1 {
  font-weight: var(--fw3);
  margin-bottom: 1.5em;
}

.checkout .left label {
  font-size: var(--font-small);
  text-transform: capitalize;
  position: relative;
  width: fit-content;
}

.checkout .left label span {
  position: absolute;
  top: 0;
  right: -10px;
  width: 6px;
  height: 6px;
  background-color: var(--primary-color);
  border-radius: var(--percent50);
}

.checkout p :where(input, textarea) {
  background-color: rgba(255 255 225 / 50%);
  font-family: inherit;
}

.checkout p input:focus {
  background-color: var(--white-color);

}

.checkout p.checkset {
  flex-direction: row;
}

.checkout p.checkset label {
  margin: 0 0 0 1em;
  cursor: pointer;
}

.checkout h2 {
  padding-top: 0.5em;
  margin-bottom: 0.75em;
  border-top: 1px solid var(--border-color);
}

.primary-checkout button {
  display: block;
  border: 0;
  outline: 0;
  cursor: pointer;
  width: var(--percent100);
  max-width: 288px;
  font-size: 1.2em;
  margin: 3em auto 0;
}

.checkout .item.right {
  padding: 5em 0 0;

}

.summary-totals ul {
  max-width: 335px;
  line-height: 2;
  margin-bottom: 3em;
  padding-bottom: 2em;
  border-bottom: 1px solid var(--secondary-dark-color);
}

.summary-totals li {
  display: flex;
  justify-content: space-between;
  font-weight: var(--fw5);
}

.summary-totals li strong {
  font-size: 2.5em;
  line-height: 1;
}

.summary-totals li:last-child {
  font-size: 2em;
}

.summary-totals li:not(:last-child) span:first-child {
  color: var(--light-text-color);
}

.summary-order .price {
  display: flex;
  flex-direction: column;
  color: var(--light-text-color);
  font-size: 0.85em;
  margin-top: 0.5em;
}

/*========== 
* MODAL
============*/
.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 600px;
  width: 90%;
  height: max-content;
  padding: 2em;
  margin: auto;
  text-align: center;
  background-color: var(--light-bg-color);
  z-index: 1000;
  visibility: hidden;
  opacity: 0;
  transform: translateY(20px);
  transition: transform .2s, visibility .2s, opacity .2s;
}

.showmodal .modal {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
  transition: transform .1s, visibility .1s, opacity .1s;
}

.modal .image {
  position: relative;
  height: calc(150px + 10vw);
  overflow: hidden;
  max-height: 400px;
  margin: -2em -2em 0 -2em;
}

.modal h2 {
  font-size: 1.75em;
  text-transform: uppercase;
  max-width: 300px;
  margin: 0.5em auto;
  line-height: 1;
}

.modal p {
  margin-bottom: 1em;
  color: var(--light-text-color);
}

.modal .t-close {
  position: absolute;
  top: 1em;
  right: 1em;
}

.backtotop a {
  position: fixed;
  bottom: 6em;
  right: 2em;
  text-align: center;
  gap: 0;
  font-size: var(--font-small);
  padding: 0.5em 1em;
  border-radius: 3px;
  z-index: 999;
  background-color: var(--light-bg-color);
  transition: var(--trans-background-color);
}

.backtotop a:hover {
  background-color: var(--dark-color);
  color: var(--white-color);
}

/*========== 
* RESPONSIVE
============*/

@media screen and (max-width: 481px) {

  .walletbutton {
    height: 39px !important;
    min-width: 150px !important;
    width: 100%;
  }

  .products.main .item {
    flex: 0 0 50%;
  }

  /* page single */
  .products.one .big-image {
    margin-bottom: 2em;
  }

  .products.one .image-show {
    height: 680px;
  }

  .products.one .thumbnail-show {
    height: 160px;
  }

  .slider h4 {
    color: var(--white-color);
    font-size: 0.5em;
  }

}

@media screen and (max-width:639px) {


  #cart-table thead {
    display: none;
  }

  #cart-table tbody tr {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    align-items: center;
  }

  #cart-table tbody tr td:first-child {
    width: var(--percent100);
    min-width: var(--percent100);
    position: relative;
  }

  #cart-table tbody tr td:not(:first-child) {
    flex-basis: 0;
    flex-grow: 1;
    max-width: var(--percent100);
  }

  #cart-table tbody tr td:last-child {
    position: absolute;
    top: 0;
    right: 0;
  }

  .donation .wallet span {
    color: white;
    background: #ffa500;
    padding: 8px 25px;
    font-size: 8px;
    border-radius: 4px;
    display: inline-block;
    cursor: pointer;
    font-weight: bold;
  }
}

@media screen and (min-width: 768px) {

  .donation .row {
    width: 75%;
    margin: 0 auto;
  }

  .donation .row h2 {
    font-size: 22px;
  }

  .lead {
    font-size: 1.5em;
    line-height: 2em
  }

  .border {
    flex: 0 0 50%;
  }

  .text-content :where(h2, h3) span:last-child {
    letter-spacing: 25px;
  }

  .products .price .current {
    font-size: 1.25em;
  }

  .products.mini,
  .banner .row,
  .widgets .row,
  .checkout .item {
    flex: 0 0 50%;
  }

  .products.main .item {
    flex: 0 0 33.3333%;
  }

  .products.one .row {
    flex: 0 0 50%;
    width: 50%;
  }

  .products.one .is_sticky,
  .is_sticky {
    position: sticky;
    top: 2em;
  }

  .checkout .item.right {
    padding: 5em 0 0 5em;
  }
}

@media screen and (min-width: 992px) {
  body {
    font-size: 16px;
  }

  .roadmap .flexend {
    justify-content: flex-end;
  }

  .donation .row {
    width: 50%;
    margin: 0 auto;
  }

  .donation .row h2 {
    font-size: 36px;
  }

  .token .main-div {
    width: 90%;
    height: 80vw;
    max-width: 800px;
    max-height: 700px;
    position: relative;
    background-color: #ff9635;
    color: var(--dark-color);
    width: 50%;
    margin: 0 auto;
    z-index: 20;
    padding: 2em;
    -webkit-box-shadow: 8px 5px 0px 0px #ff9635a8;
    box-shadow: 8px 5px 0px 0px #ff9635a8;
  }

  .container {
    padding: 0 2em;
  }

  .desktop-hide {
    display: none;
  }

  .mobile-hide {
    display: block;
  }

  .logo a {
    margin-right: 2em;
  }

  .header-nav {
    padding: 0;
    margin: 0;
    border-bottom: 0;
  }

  .header-nav .right li>a {
    margin-left: 1em;
  }

  nav .mega {
    position: absolute;
    width: var(--percent100);
    height: auto;
    left: 0;
    right: 0;
    padding: 2.5em;
    line-height: 2em;
    background-color: var(--light-bg-color);
    box-shadow: rgb(0 0 0 / 20%) 0 30px 20px -30px;
    z-index: 100;
    display: none;
  }

  nav li.has-child:hover .mega {
    display: block;
  }

  nav .mega .wrapper {
    display: flex;
  }

  nav .mega h4 {
    font-size: 0.8em;
    text-transform: uppercase;
  }

  nav .mega ul {
    font-size: var(--font-small);
  }

  nav .mega .women-brands {
    display: flex;
    flex-wrap: wrap;
    max-width: 100px;
  }

  nav .mega .women-brands li {
    min-width: 80px;
  }

  nav .mega .view-all {
    margin-top: 1em;
  }

  nav .mega .products {
    flex: 2;
    padding: 0;
    align-items: center;
    position: relative;
  }

  nav .mega .products .row {
    width: var(--percent100);
  }

  nav .mega .products .media {
    height: 400px;
  }

  nav .mega .products .text-content {
    line-height: initial;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5em;
    margin-top: 0.5em;
    position: absolute;
    bottom: 4em;
    width: var(--percent100);
  }

  nav .mega .products .text-content h4 {
    font-size: 2em;
    font-weight: var(--fw8);
    text-align: center;
    color: var(--secondary-dark-color);
  }

  .header-main .right {
    max-width: 600px;
    margin-left: auto;
  }

  /* Department menu */

  .dpt-menu {
    position: absolute;
    top: var(--percent100);
    width: 300px;
    background-color: var(--white-color);
    border: 1px solid var(--border-color);
    border-top: 0;
    border-bottom: 0;
  }

  .dpt-menu>ul>li>a {
    font-weight: var(--fw5);
    padding: 0 1.5em;
    border-bottom: 1px solid var(--border-color);
  }

  .dpt-menu .has-child>ul,
  .dpt-menu .mega {
    position: absolute;
    top: 0;
    left: var(--percent100);
    width: var(--percent100);
    height: auto;
    min-width: var(--percent100);
    padding: 1.5em;
    font-size: var(--font-small);
    line-height: 2.5em;
    border: 1px solid var(--border-color);
    border-top: 0;
    display: none;
    background-position: right bottom;
    background-repeat: no-repeat;
    background-size: auto;
  }

  /* show the first submenu  */

  .dpt-menu .has-child:nth-child(1)>ul {
    display: hidden;
  }




  .dpt-menu .mega {
    width: auto;
    min-width: var(--percent100);
  }

  .dpt-menu .has-child> :where(ul, .mega)::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(255, 255, 225, 0) 100%);
  }

  .dpt-menu .has-child:hover .mega {
    display: flex;
  }

  /* slider */

  .slider>div>.wrapper {
    margin-left: auto;
  }

  /* products */

  .trending .products,
  .product-categories .row {
    flex: 0 0 33.3333%;
  }

  .products.big .media {
    max-height: 373px;
  }

  .products.main .item,
  .widgets .row {
    flex: 0 0 25%;
  }

  .newsletter .box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  /* single page */

  #page:not(.page-home) .dpt-menu {
    display: none;
  }

  #page.showdpt .dpt-menu {
    display: block;
  }

  .products.one .flexwrap>.row:last-child>.item {
    padding-left: 2em;
  }

  /* Page Category */
  .single-category .holder {
    flex-direction: row;
  }

  .single-category .sidebar {
    flex: 1 0 25%;
  }

  .single-category .section {
    flex: 1 0 75%;
  }

  .single-category .sidebar .filter {
    padding-right: 1.5em;
  }

  .single-category .sidebar .filter {
    padding: 0 1.5em 0 0;
    position: sticky;
    top: 2em;
    box-shadow: none;
    width: var(--percent100);
    z-index: initial;
    visibility: visible;
    opacity: 1;
  }

  .single-category .products.main .item {
    flex: 0 0 33.3333%;
  }

  /* page cart */
  .products.cart .form-cart {
    width: 66%;
  }

  .products.cart .cart-summary {
    width: 34%;
    padding-left: 2.5em;
    margin-top: 0;
  }
}